@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  background: -webkit-gradient(linear, left top, left bottom, from(#996633), to(#000066));
  background: -webkit-linear-gradient(top, #996633 0%, #000066 100%);
  background: linear-gradient(to bottom, #996633 0%, #000066 100%);
  width: 100%;
  min-height: 100vh;
  font-family: 'Raleway', sans-serif;
  color: #e0d7d7
}

#app-root {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.task-board {
  box-sizing: border-box;
  text-align: center;
  width: 60vw;
  min-height: 70vh;
  border: 2px solid #e0d7d7;
  margin: 15px 0;
}

.task-board_title {
  margin: 18px 0;
  font-size: 50px;
}

.task-board_buttonSection > button {
  margin: 10px 10px 20px;
  width: 90px;
  height: 29px;
  background: none;
  border: none;
  border: 1px solid #d4d4d4;
  color: #e0d7d7;
  outline: none;
  font-size: 18px;
}

.task-board_buttonSection > button:hover {
  border: 2px solid #ffffff;
  color: #ffffff;
}

.task-board_list {
  text-align: center;
  font-size: 20px;
  padding: 0;
}

@media only screen and (max-width: 799px) {
  .task-board_title {
    font-size: 35px;
  }

  .task-board_buttonSection > button {
    font-size: 13px;
    width: 70px;
    height: 21px;
  }

  .task-board_list {
    font-size: 15px;
  }
}
.task-board_list_singleItem {
    list-style: none;
    text-align: center;
}

.task-board_list_singleItem-text {
    margin: 0 0 15px;
}


